import React, {useState, useEffect} from "react";
import articlesContent from './articles-content';
import ArticlesList from "../components/ArticlesList";
import NotFoundPage from "./NotFondPage";
import CommentsList from "../components/CommentsList";
import UpvotesSection from "../components/UpvotesSection";
import AddComment from "../components/AddComment";


const ArticlesDetails = ({match}) => {
  const name = match.params.name;
  const content = articlesContent.find(item => item.name === name);
  const otherContent = articlesContent.filter(item => item.name !== name);

  const [article, setArticle] = useState({});
  useEffect(() => {
    const fetchData = async () => {
      const result = await fetch(`/api/articles/${name}`);
      const body = await result.json();
      setArticle(body);
    };
    fetchData();
  }, [name])
  if( !content ) {
    return( <NotFoundPage />)
  }
 
  return(
    <>
      <h1>{content.title}</h1>
      <UpvotesSection
        setArticle={setArticle}
        articleName={article.name}
        upvotes={article.upvotes}
      />
      {content.content.map((item, index) => (
        <p key={index}>
          {item}
        </p>
      ))}
      <AddComment
        articleName={name}
        setArticle={setArticle}
      />
      {article.comments ? <CommentsList comments={article.comments}/>: ""}
      <h3> Hot Content</h3>
      <ArticlesList articles={otherContent} />
    </>
  );
  } 

export default ArticlesDetails;