import React from 'react';

const UpvotesSection = ({articleName, setArticle, upvotes}) =>{

  const upvotesArticles = async () => {
    const res = await fetch(`/api/articls/${articleName}/upvotes`, {
      method: 'POST',
    });
    const updateInfo = await res.json();
    setArticle(updateInfo);
  };

  return (
    <div>
      <button onClick={() => upvotesArticles()}>Add Upvotes</button>
      <p>
        This post has been upvoted {upvotes} times
      </p> 
    </div>
  );
};

export default UpvotesSection;